/* button css start */
.cmn-btn {
  padding: 12px 35px;
  @include border-radius(5px);
  background-color: $base-color;
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.15);
  @include transition(all 0.3s);
  color: #ffffff;
  &:hover {
    color: #ffffff;
    background-color: darken($base-color, 10%); }
  &.active {
    background-color: $bg-one; } }
.cmn-btn2 {
  padding: 12px 35px;
  @include border-radius(5px);
  background-color: $base-color-two;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.15);
  @include transition(all 0.3s);
  color: #ffffff;
  &:hover {
    color: #ffffff;
    background-color: darken($base-color-two, 5%); } }
.btn--danger {
  background-color: #ff3535 !important;
  &:hover {
    background-color: darken(#ff3535, 10%) !important; } }
.border-btn {
  padding: 12px 35px;
  color: $para-color;
  @include border-radius(5px);
  border: 1px solid #e5e5e5;
  @include transition(all 0.3s);
  &:hover {
    background-color: $base-color;
    color: #ffffff;
    border-color: $base-color;
    box-shadow: $shadow; } }
.read-btn {
  padding: 10px 20px;
  min-width: 150px;
  font-size: 14px;
  font-weight: 600;
  background-color: rgba($base-color, 0.14);
  color: $base-color;
  position: relative;
  text-align: center;
  @include border-radius(5px);
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  @include transition(all 0.3s);
  &:hover {
    background-color: $base-color;
    color: #ffffff;
    i {
      background-color: #ffffff;
      color: $base-color; } }
  i {
    background-color: $base-color;
    color: #ffffff;
    font-size: 18px;
    margin-left: 15px;
    padding: 6px 6px;
    @include border-radius(5px);
    @include transition(all 0.3s); } }

.btn-group {
  @include d-flex;
  margin-left: -10px;
  margin-right: -10px;
  *[class*="btn"] {
    margin: 5px 10px;
    align-items: center;
    &.d-flex {
      padding: 8px 35px; } } }
[class*="btn"] {
  &.btn-md {
    padding: 10px 30px; }
  &.btn-sm {
    padding: 6px 20px; } }

.action-btn {
  padding: 5px 15px;
  font-weight: 600;
  background-color: #ffffff;
  color: $base-color;
  font-size: 14px;
  @include border-radius(5px); }

.bg-btn {
  border: 2px solid #ffffff;
  color: #ffffff;
  padding: 12px 25px;
  &:hover {
    background-color: $bg-one;
    color: #ffffff;
    border-color: $bg-one; } }
/* button css end */
